/* üé® Estilos globales de la aplicaci√≥n */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased; /* Suavizado de texto en navegadores WebKit */
  -moz-osx-font-smoothing: grayscale; /* Suavizado de texto en macOS */
}

/* üíª Estilo para el texto dentro de etiquetas <code> */
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
