/* =========================================================
   üéÆ Layout General
   ========================================================= */
.contenedor-biblioteca {
  display: flex;
  min-height: 100vh;
  background-color: #0f172a;
  color: #f1f5f9;
  font-family: "Inter", sans-serif;
}

/* =========================================================
   üìÇ Sidebar
   ========================================================= */
.barra-lateral {
  width: 230px;
  background: #273549;
  padding: 1.8rem 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-right: 1px solid #334155;
}

.menu-lateral {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}

.boton-lateral {
  display: flex;
  align-items: center;
  gap: 10px;
  color: #cbd5e1;
  text-decoration: none;
  font-size: 0.95rem;
  padding: 0.7rem 1rem;
  border-radius: 8px;
  transition: all 0.3s ease;
}

.boton-lateral:hover {
  background: #334155;
  color: #ffffff;
}

.menu-lateral li.activo .boton-lateral {
  background: #14b8a6;
  color: #ffffff;
  font-weight: 600;
}

/* üìä Estad√≠sticas r√°pidas */
.estadisticas-rapidas {
  background: #334155;
  padding: 1rem;
  border-radius: 10px;
  color: #e2e8f0;
  font-size: 0.85rem;
  margin-top: 2rem;
}

/* =========================================================
   üìò Contenido Principal
   ========================================================= */
.contenido-principal {
  flex: 1;
  display: flex;
  flex-direction: column;
  background-color: #0f172a;
}

.encabezado-biblioteca {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #273549;
  padding: 1rem 2rem;
  border-bottom: 1px solid #334155;
}

.encabezado-biblioteca h1 {
  font-size: 1.4rem;
  font-weight: 700;
}

/* ‚ûï Bot√≥n agregar */
.add-btn {
  background: #14b8a6;
  color: #ffffff;
  border: none;
  padding: 10px 18px;
  font-weight: 600;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 6px;
}

.add-btn:hover {
  background: #0d9488;
}

/* =========================================================
   üÉè Tarjetas de Juegos
   ========================================================= */
.rejilla-juegos {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(270px, 1fr));
  gap: 1.8rem;
  width: 100%;
  max-width: 1250px;
  margin: 2rem auto;
}

.tarjeta-juego-pro {
  background: #1e293b;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.25);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  position: relative;
  color: #f8fafc;
}

.tarjeta-juego-pro:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.35);
}

.imagen-juego {
  width: 100%;
  height: 15rem;
  object-fit: cover;
}

/* ‚≠ê Etiquetas de estado en espa√±ol */
.estado-juego {
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 4px 10px;
  font-size: 0.75rem;
  font-weight: 700;
  border-radius: 6px;
  color: #fff;
  text-transform: capitalize;
}

.estado-pendiente { background-color: #7a7a7a; }
.estado-jugando { background-color: #16a34a; }
.estado-completado { background-color: #1d4ed8; }
.estado-abandonado { background-color: #dc2626; }

.info-juego {
  padding: 1rem;
}

.info-juego h3 {
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 0.3rem;
}

.info-juego .genero {
  font-size: 0.9rem;
  color: #94a3b8;
  margin-bottom: 0.4rem;
}

/* ‚≠ê Estrellas */
.estrellas span {
  color: #475569;
  font-size: 1.1rem;
}

.estrellas span.activa {
  color: #facc15;
}

/* Horas */
.horas {
  color: #9ca3af;
  font-size: 0.9rem;
  text-align: center;
}

/* =========================================================
   ‚úÇÔ∏è Botones Overlay Editar/Eliminar
   ========================================================= */
.contenedor-imagen {
  position: relative;
  overflow: hidden;
}

.overlay-botones {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.65);
  opacity: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  transition: opacity 0.3s ease;
}

.contenedor-imagen:hover .overlay-botones {
  opacity: 1;
}

.btn-azul, .btn-rojo {
  border: none;
  padding: 0.6rem 1rem;
  border-radius: 8px;
  color: white;
  font-weight: 500;
  cursor: pointer;
}

.btn-azul { background: #3b82f6; }
.btn-azul:hover { background: #2563eb; }

.btn-rojo { background: #ef4444; }
.btn-rojo:hover { background: #dc2626; }

/* =========================================================
   ü™ü MODAL
   ========================================================= */
.modal-fondo {
  position: fixed;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2000;
}

.modal {
  background: #1e2530;
  color: #fff;
  padding: 2rem;
  border-radius: 12px;
  width: 400px;
  max-width: 90%;
  box-shadow: 0 0 15px rgba(0,0,0,0.4);
}

.formulario-juego {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}

.formulario-juego input,
.formulario-juego textarea,
.formulario-juego select {
  background-color: #2b3440;
  border: 1px solid #334155;
  padding: 0.6rem;
  border-radius: 6px;
  color: #fff;
}

.formulario-juego input:focus,
.formulario-juego textarea:focus,
.formulario-juego select:focus {
  outline: 2px solid #00c9a7;
}

/* Botones modal */
.botones-formulario {
  display: flex;
  justify-content: space-between;
  margin-top: 1rem;
}

.guardar-btn {
  background-color: #00c9a7;
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 6px;
}

.cancelar-btn {
  background-color: #ff4757;
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 6px;
}
/* ===========================
   üìä ESTADISTICAS DASHBOARD 
   =========================== */

.contenedor-estadisticas {
  display: flex;
  min-height: 100vh;
  background-color: #0f172a;
  color: #e2e8f0;
}

.contenido-estadisticas {
  flex: 1;
  padding: 2rem;
}

.encabezado-estadisticas h1 {
  font-size: 1.6rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
}

/* CARDS */
.tarjetas-estadisticas {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 1.2rem;
  margin-bottom: 2.5rem;
}

.tarjeta {
  background: #1e293b;
  border: 1px solid #334155;
  padding: 1.2rem;
  border-radius: 10px;
  text-align: center;
  font-weight: 600;
  box-shadow: 0 4px 10px rgba(0,0,0,0.25);
}

.tarjeta span {
  font-size: 1.7rem;
  color: #14b8a6;
  display: block;
  margin-top: 6px;
}

/* GRAFICOS */
.graficos-estadisticas {
  display: grid;
  gap: 2rem;
}

.grafico {
  background: #1e293b;
  border: 1px solid #334155;
  padding: 1.2rem;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.25);
}

.grafico h3 {
  margin-bottom: .8rem;
  font-weight: 600;
  font-size: 1.1rem;
}
.btn-verde {
  background: #22c55e;
  padding: 0.6rem 1rem;
  border-radius: 8px;
  color: white;
  font-weight: 500;
}
.btn-verde:hover { background:#16a34a; }

.lista-rese√±as {
  max-height: 300px;
  overflow-y: auto;
  margin-top: 1rem;
}

.item-rese√±a {
  background:#1e293b;
  border-radius:8px;
  padding:10px;
  margin-bottom:10px;
}
